// File for scss-vars.
// For example: main colors, paddings and so on.

$main-color: #202527;
$light-links: #545658;
$light-text: #94999e;
$bg-gray: #fbfbfc;
$bg-gray-mobile: #f7f7f9;
$border-color: #e9eaec;
$border-color2: #d1d1d1;
$blue: #0096ea;
$red: #ea096e;
$gray: #828282;

//   https://colorscheme.ru/color-converter.html

$black: hsl(0, 0%, 0%);
$gray-darkest: hsl(0, 0%, 10%);
$gray-darker: hsl(0, 0%, 20%);
$gray-darken: hsl(0, 0%, 30%);
$gray-dark: hsl(0, 0%, 40%);
$gray: hsl(0, 0%, 50%);
$gray-light: hsl(0, 0%, 60%);
$gray-lighten: hsl(0, 0%, 70%);
$gray-lighter: hsl(0, 0%, 80%);
$gray-lightest: hsl(0, 0%, 90%);
$white: hsl(0, 0%, 100%);

$color-main: #048bcd;
$color-success: hsl(120, 39%, 54%);
$color-danger: hsl(2, 64%, 58%);
$color-warning: hsl(35, 84%, 62%);

$color-vk: #4a76a8;
$color-fb: #4267b2;
$color-tw: #40a2f5;
$color-gh: #24292e;

//  

$text-color: #36353a;
$text-color-muted: #7c8185;
$body-bg: #f0f0f0;

$link-color: $color-main;
$link-color--hover: darken($color-main, 15%);

//  

$font-size: 16px;

$font-size-h1: 40px;
$font-size-h2: 32px;
$font-size-h3: 30px;
$font-size-h4: 18px;
$font-size-h5: 16px;
$font-size-h6: 16px;

$font-size-sm: .75em; // 12px  16px

$line-height: 1.5;

$typo-margin-vertical: 1em;

$font-family: -apple-system, BlinkMacSystemFont, 'Roboto', 'Ubuntu', 'Droid Sans', 'Helvetica Neue',
    'Arial', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
$font-family--monospace: SFMono-Regular, Menlo, Monaco, Consolas, Liberation Mono, Courier New,
    monospace;
// Georgia, Times New Roman, Times, serif

// 

$screen-xs: 0;
$screen-sm: 480px;
$screen-md: 768px;
$screen-lg: 992px;
$screen-xl: 1350px;
$screen-xxl: 1350px;

$container-sm: 100%;
$container-md: 100%;
//$container-lg:                $screen-lg - 30;
$container-lg: 100%;
$container-xl: $screen-xl - 30;
$container-xxl: $screen-xxl - 30;

//  

$grid-columns: 12;
$grid-gutter-width: 40px;

// 

$field-padding-vertical: 3px;
$field-padding-horizontal: 12px;

$border-radius: 4px;
$opacity: .7;
$transition-time: .3s;
$shadow: 0 4px 2px -2px rgba(0, 0, 0, .3);

$menu-desktop-width: $screen-lg;
$toggler-part-height: 1px;
$toggler-part-bg-color: $color-main;

$z-index-modal: 100;
